import '../../react-masonry.css'

import React from "react"
import { graphql } from 'gatsby'

import ArtHeader from '../../components/ArtHeader'
import MasonryGallery from '../../components/MasonryGallery'
import { spacer } from "../../common"


const ArtPage = ({data}) => {
  	let images = data.allFile.edges
    return (
    	<React.Fragment>
    			<ArtHeader
            generator='AI-generated Artworks'
            byline='Latest digital artworks generated from text prompts (never edited). Click links below for more.'
          />
    			<MasonryGallery images={images} />
	        <div className="spacer" style={spacer}></div>
    	</React.Fragment>
    )
}


export const query = graphql`
query ArtworksQuery {
  allFile(
  	sort: {fields: birthTime, order: DESC},
  	limit: 25,
    filter: {
      extension: { regex: "/(jpg)|(png)/" }
    }
  ) {
    edges {
      node {
        childImageSharp {
          gatsbyImageData(
            width: 400
						placeholder: DOMINANT_COLOR
          )
        }
      }
    }
  }
}`

export default ArtPage
